<template>
  <div>
    <b-card body-class="bodyCard" id="myCard">
      <b-row class="mb-4 mt-1">
        <b-col>
          <b-img
            width="100"
            height="100"
            id="logo"
            :src="require(`@/assets/${content.logo}`)"
          ></b-img>
        </b-col>
      </b-row>
      <b-row class="mb-5">
        <b-col id="titleProjeto" class="text-center">
          {{ content.title }}
        </b-col>
      </b-row>
      <b-row class="my-4">
        <b-col>
          <b-icon-gear v-if="content.dev" id="iconGear" scale="4" :rotate="rotacao" />
          <b-icon-check2-circle v-else id="iconGear" scale="4" />
        </b-col>
      </b-row>
      <b-row class="mt-4 mb-3">
        <b-col v-if="content.dev" id="estadoDesenvolvimento">
          Em desenvolvimento
        </b-col>
        <b-col v-else id="estadoDesenvolvimento">
          Concluído
        </b-col>
      </b-row>
      <b-row align-h="center" id="linhaTags">
        <b-col v-for="(tags, index) in content.tag" :key="index" cols="auto">
          <tag :content="tags" />
        </b-col>
      </b-row>
      <b-row align-h="end" class="mt-3">
        <b-col cols="auto" id="link">
          <b-link
            :class="content.dev ? 'disableLink' : 'enableLink'"
            :disabled="content.dev"
            :href="content.url"
            target="_blank"
          >
            Link<b-icon-arrow-right scale="1.2" id="icon" />
          </b-link>
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>

<style lang="scss">
#linhaTags {
  .col-auto {
    padding: 0 5px;
  }
}
</style>

<style lang="scss" scoped>
#myCard {
  background-color: #242424;
  border-radius: 20px;
  width: 250px;
}
#titleProjeto {
  color: #f0f0f0;
  font-size: 1rem;
}
#estadoDesenvolvimento {
  color: #f0f0f0;
  font-size: 0.9rem;
}
.enableLink {
  color: #f0f0f0 !important;
  text-decoration: underline;
}
.disableLink {
  color: #8a8a8a;
  pointer-events: none;
  text-decoration: underline;
}
#iconGear {
  color: #f0f0f0;
}
#icon {
  margin-left: 8px;
}
@media (max-width: 600px) {
  #myCard {
    width: 200px;
  }

  #titleProjeto {
    font-size: 0.9rem;
  }
  #estadoDesenvolvimento {
    font-size: 0.7rem;
  }
  #link {
    font-size: 0.8rem;
  }
  #logo {
    width: 60px;
    height: 60px;
  }
}
</style>

<script>
import tag from "@/components/public/projects/tag.vue";
export default {
  components: {
    tag,
  },
  data() {
    return {
      rotacao: 0,
    };
  },
  props: {
    content: {
      type: Object,
      required: true,
    },
    dev: {
      type: Boolean,
      required: true,
    },
  },
  created() {
    setInterval(() => {
      this.rotacao += 10;
      if (this.rotacao >= 360) this.rotacao = 0;
    }, 150);
  },
};
</script>
