<template>
  <div id="principal">
    <b-navbar
      v-scroll="colorNavScroll"
      :class="scrolled ? 'scrolled' : ''"
      fixed="top"
      id="navbar"
      toggleable="md"
    >
      <b-navbar-brand href="#" class="ml-lg-5">
        <b-img width="60px" id="logo" :src="require('@/assets/Logo.png')" />
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse">
        <template v-slot:default="{ expanded }">
          <b-icon v-if="expanded" icon="x" class="icone-toggle" font-scale="2"></b-icon>
          <b-icon v-else icon="list" class="icone-toggle" font-scale="2"></b-icon>
        </template>
      </b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav id="navbarNav" class="ml-auto mr-md-5">
          <b-nav-item @click="scrollUpper('sobreMim')" class="mx-2 itemNav">
            Início
          </b-nav-item>
          <b-nav-item @click="scrollUpper('projetos')" class="mx-2 itemNav">
            Projetos
          </b-nav-item>
          <b-nav-item @click="scrollUpper('habilidades')" class="mx-2 itemNav">
            Habilidades
          </b-nav-item>
          <b-nav-item @click="scrollUpper('contato')" class="mx-2 itemNav">
            Contato
          </b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<style>
html {
  scroll-behavior: smooth;
}
</style>

<style lang="scss" scoped>
#navbar {
  background-color: #f0f0f0;
}

#navbarNav {
  font-size: 1.5rem;
}

#logo {
  filter: invert(100%);
  transition: 500ms;
}

@media (max-width: 600px) {
  #navbarNav {
    font-size: 1.3rem;
  }

  #logo {
    width: 50px;
  }
}

/*Scroll*/
.scrolled#navbar {
  background-color: #242424;
  transition: 500ms ease-in-out;
}

.scrolled {
  #logo {
    filter: invert(0%);
  }
}
</style>

<style lang="scss">
.scrolled .itemNav {
  a {
    color: #f0f0f0 !important;
  }
  a:after {
    background: #f0f0f0 !important;
  }
}
.itemNav {
  a {
    color: #242424 !important;
    position: relative;
    width: auto;
    display: inline-block;
  }

  a:after {
    background: none repeat scroll 0 0 transparent;
    bottom: 8;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #242424;

    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
  }
  a:hover:after {
    width: 100%;
    left: 0;
  }
}
</style>

<script>
export default {
  data() {
    return {
      scrolled: false,
    };
  },
  directives: {
    scroll: {
      inserted: function(el, binding) {
        let f = function(evt) {
          if (binding.value(evt, el)) {
            //remove o evento
            window.removeEventListener("scroll", f);
          }
        };
        //adiciona o evento
        window.addEventListener("scroll", f);
      },
    },
  },
  methods: {
    colorNavScroll() {
      if (window.scrollY > 50) {
        this.scrolled = true;
      } else {
        this.scrolled = false;
      }
    },
    scrollUpper(id) {
      let posTop = document.getElementById(id).offsetTop;
      let elementoNav = document.getElementById("navbar");
      let tamanhoNav = elementoNav.offsetHeight;

      window.scrollTo(0, posTop - tamanhoNav);
    },
  },
};
</script>
