<template>
  <div id="contato">
    <b-container>
      <b-row align-h="center">
        <b-col cols="auto" id="title">Contato</b-col>
      </b-row>
      <b-row align-h="center">
        <b-col
          cols="auto"
          @click="copyContent('josev.marques01@gmail.com')"
          id="email"
          v-b-tooltip.click.rightbottom="'copiado'"
          class="mt-2"
        >
          <b-icon-files id="iconPaste" />josev.marques01@gmail.com
        </b-col>
      </b-row>
      <b-row align-h="center" class="mt-4">
        <b-col cols="auto" class="mr-3">
          <b-link href="https://github.com/josev-marques" target="_blank">
            <b-img class="icon" :src="require('@/assets/github.svg')" />
          </b-link>
        </b-col>
        <b-col cols="auto" class="mr-3">
          <b-link href="https://gitlab.com/josev.marques" target="_blank">
            <b-img class="icon" :src="require('@/assets/gitlab.svg')" />
          </b-link>
        </b-col>
        <b-col cols="auto" class="mr-3">
          <b-link
            href="https://mail.google.com/mail/?view=cm&source=mailto&to=josev.marques01@gmail.com"
            target="_blank"
          >
            <b-img class="icon" :src="require('@/assets/email.svg')" />
          </b-link>
        </b-col>
        <b-col cols="auto">
          <b-link
            href="https://www.linkedin.com/in/jos%C3%A9-v%C3%ADtor-andrade-marques-539484181/"
            target="_blank"
          >
            <b-img class="icon" :src="require('@/assets/linkedin.svg')" />
          </b-link>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<style lang="scss" scoped>
#contato {
  padding: 30px 0;
  background-color: #242424;
}
#title {
  font-size: 1.5rem;
  color: #f0f0f0;
}
.icon {
  filter: invert(100%) sepia(25%) saturate(180%) hue-rotate(200deg) brightness(115%) contrast(88%);
}
.icon:hover {
  filter: invert(0%) sepia(25%) saturate(180%) hue-rotate(200deg) brightness(115%) contrast(88%);
  transition: 300ms;
}
#email {
  color: #f0f0f0;
  font-size: 1rem;
  cursor: pointer;
}
@media (max-width: 600px) {
  #title {
    font-size: 1.5rem;
  }
  #email {
    font-size: 0.8rem;
  }
  .icon {
    width: 35px;
    height: 35px;
  }
}
#iconPaste {
  padding-top: 3px;
}
</style>

<script>
export default {
  methods: {
    copyContent(_str) {
      setTimeout(() => {
        this.$root.$emit("bv::hide::tooltip", "email");
      }, 2000);
      //Função para copiar para a área a tranferência a parâmetro "str"
      const element = document.createElement("textarea");
      element.value = _str;
      document.body.appendChild(element);
      element.select();
      document.execCommand("copy");
      document.body.removeChild(element);
    },
  },
};
</script>
